@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
.profile {
  width: 250px;
  height: 330px;
}


.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 5px;
}

.name,
.tag,
.location {
  font-family: "Lato", sans-serif;
  margin: 0;
  padding: 0;
  line-height: 32px;
}

.name {
  font-size: 18px;
  color: black;
  font-weight: 800;
}

.tag,
.location {
  font-size: 14px;
  color: #768696;
  font-weight: 400;
}

.location {
  margin-bottom: 0;
}

.avatar {
  border-radius: 50%;
  margin-top: 30px;
  margin-bottom: 24px;
}

.stats {
  display: flex;
  flex-wrap: nowrap;
  list-style: none;
  margin: 0;
  padding: 0;
}

.stats > li {
  flex-basis: 100px;
  display: flex;
  flex-wrap: wrap;
  height: 73px;
  border: 1px solid #e4e9f0;
  background-color: #f3f6f9;
}

.label,
.quantity {
  flex-basis: 100%;
  text-align: center;
  font-family: "Lato", sans-serif;
  line-height: 20px;
}

.label {
  font-weight: 400;
  font-size: 12px;
  color: #768696;
  margin-top: auto;
}

.quantity {
  font-weight: 900;
  font-size: 14px;
  color: #1f3349;

}
